import { NextResponse } from 'next/server';
import dbConnect from '@/lib/db';
import User from '@/models/User';
import bcrypt from 'bcryptjs';

export async function POST(req: Request) {
  await dbConnect();

  try {
    const { name, email } = await req.json();

    if (!name || !email) {
      return NextResponse.json({ error: 'Name and email are required' }, { status: 400 });
    }

    const existingUser = await User.findOne({ email });
    if (existingUser) {
      return NextResponse.json({ error: 'User with this email already exists' }, { status: 400 });
    }

    // Generate a random password
    const rawPassword = Math.random().toString(36).slice(-8);
    const salt = await bcrypt.genSalt(10);
    const hashedPassword = await bcrypt.hash(rawPassword, salt);

    const lecturer = await User.create({
      name,
      email,
      password: hashedPassword,
      role: 'LECTURER',
      avatar: `https://ui-avatars.com/api/?name=${encodeURIComponent(name)}&background=random`,
    });

    // Simulate sending email
    console.log('--- EMAIL SIMULATION ---');
    console.log(`To: ${email}`);
    console.log(`Subject: Welcome to Smart Attendance Pro`);
    console.log(`Body: Hello ${name}, your lecturer account has been created. Login with:`);
    console.log(`Email: ${email}`);
    console.log(`Password: ${rawPassword}`);
    console.log('------------------------');

    return NextResponse.json({
      message: 'Lecturer created successfully',
      lecturer: {
        id: lecturer._id,
        name: lecturer.name,
        email: lecturer.email,
        role: lecturer.role,
      },
      // Returning credentials here only for demonstration/verification since we don't have real email
      credentials: {
        email,
        password: rawPassword
      }
    }, { status: 201 });

  } catch (error: any) {
    return NextResponse.json({ error: error.message }, { status: 500 });
  }
}
